<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">  </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">  </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title"></h1>
      <span class="content__info">{{ textTotalItems(totalItems) }}</span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST"
        @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              title=""
              placeholder="   "
              :error="formError.name"
              v-model="formData.name"
            />
            <BaseFormText
              title=" "
              placeholder="  "
              :error="formError.address"
              v-model="formData.address"
            />
            <BaseFormText
              title=""
              placeholder="  "
              :error="formError.phone"
              type="tel"
              v-model="formData.phone"
            />
            <BaseFormText
              title="Email"
              placeholder="  Email"
              :error="formError.email"
              type="email"
              v-model="formData.email"
            />
            <BaseFormTextarea
              title="  "
              placeholder=" "
              :error="formError.comment"
              v-model="formData.comment"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value">  <b>500 </b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value">    </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value">    </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div v-if="formSubmitted"><BasePreloader /></div>
        <OrderItems
        v-else
        :products="products"
        :totalItems="totalItems"
        :totalPrice="totalPrice">
          <button class="cart__button button button--primery" type="submit">
             
          </button>
        </OrderItems>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import textTotalItems from '@/helpers/textTotalItems';
import OrderItems from '@/components/OrderItems.vue';
import BasePreloader from '@/components/BasePreloader.vue';
import axios from 'axios';
import API_BASE_URL from '@/config';
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formSubmitted: false,
    };
  },
  components: {
    BaseFormText, BaseFormTextarea, OrderItems, BasePreloader,
  },

  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: 'cartTotalPrice',
      totalItems: 'cartTotalItems',
    }),
  },

  methods: {
    order() {
      this.formSubmitted = true;
      this.formError = {};
      this.formErrorMessage = '';

      axios
        .post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        .then(() => {
          this.formSubmitted = false;
        });
    },
    textTotalItems,
  },
};
</script>
