<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title"></h1>
      <span class="content__info"> {{ textTotalItems(countProducts) }} </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :color.sync="filterColor"
        :page.sync="page"
      ></ProductFilter>

      <section class="catalog">
        <div v-if="productsLoading">
          <BasePreloader />
        </div>
        <div v-if="productsLoadingFailed">
          E R R O R ! ! !
          <button @click.prevent="loadProducts"></button>
        </div>

        <ProductList :products="products"></ProductList>

        <BasePagintion
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        ></BasePagintion>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagintion from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import textTotalItems from '@/helpers/textTotalItems';
import axios from 'axios';
import API_BASE_URL from '@/config';
import BasePreloader from '@/components/BasePreloader.vue';

export default {
  components: {
    ProductList,
    BasePagintion,
    ProductFilter,
    BasePreloader,
  },

  data() {
    return {
      page: 1,
      productsPerPage: 6,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    textTotalItems,
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);

      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColor,
            },
          })
          .then((response) => {
            this.productsData = response.data;
          })
          .catch(() => {
            this.productsLoadingFailed = true;
          })
          .then(() => {
            this.productsLoading = false;
          });
      }, 2000);
    },
  },
  created() {
    this.loadProducts();
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
};
</script>
