<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="  "
      @click.prevent="minus"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="amount" />

    <button
      type="button"
      aria-label="  "
      @click.prevent="plus"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['amount'],
  methods: {
    plus() {
      this.$emit('update:amount', this.amount + 1);
    },
    minus() {
      if (this.amount >= 2) {
        this.$emit('update:amount', this.amount - 1);
      }
    },
  },
};
</script>
