<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.image"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <!-- <p class="product__info">: <span>128GB</span></p> -->
    <span class="product__code"> : {{ item.product.id }} </span>

    <BasePlusMinus :amount.sync="amount" class="product__counter"/>

    <b class="product__price">
      {{ (item.amount * item.product.price) | numberFormat }} 
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="   "
      @click.prevent="deleteItem"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';
import BasePlusMinus from '@/components/BasePlusMinus.vue';

export default {
  props: ['item'],
  filters: {
    numberFormat,
  },
  components: { BasePlusMinus },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions(['deleteCartProduct']),

    deleteItem() {
      this.deleteCartProduct({ productId: this.item.productId });
    },
  },
};
</script>
